function Header(props) {
    const {
        classes,
        setHeaderValue,
        headerValue,
        location
    } = props;

    const store = Store.store

    const [value, setValue] = React.useState(0);

    const account = store.getStore('account')
    const rewardPools = store.getStore('rewardPools')
    const rewardPool = rewardPools.filter((items) => items.id === 'GovernanceV2 defi')[0]
    const rewardPoolUSDT = rewardPools.filter((items) => items.id === 'GovernanceV2USDT')[0]
    const rewardPoolDAI = rewardPools.filter((items) => items.id === 'GovernanceV2Dai')[0]
    const rewardPoolKANI = rewardPools.filter((items) => items.id === 'GovernanceV2Kani')[0]

    const handleChange = (event, newValue) => {
        setValue(newValue);
        setHeaderValue(newValue)
    };

    const nav = (screen) => {
        props.history.push('/' + screen)
    }

    const navigateStake = (rewardPool) => {
        store.setStore({currentPool: rewardPool})
        props.history.push('/stake')
        // console.log('path',props.location.pathname)
        // if(props.location.pathname !== '/stake'){
        //     console.log('23')
        // }
    }

    const [age, setAge] = React.useState('');

    const handleChangeAge = (event) => {
        setAge(event.target.value);
    };

    return (
        <div className={classes.root} id="headerRoot">
            <div className="container">
                <div className="left">
                    <div className="itemList">
                        <div className="item">
                            <Link to={"/"} className="link">
                                <div className="img-container">
                                    <div className="logo-image">
                                        <img src={kaniLogo} alt=""/>
                                    </div>
                                    <Typography className="logo-txt">Kani</Typography>
                                </div>
                            </Link>
                        </div>
                    </div>
                </div>
                <div className="right">
                    <div className="itemList">
                        <div className="item">
                            <FormControl>
                                <Select
                                    value={age}
                                    onChange={handleChangeAge}
                                    displayEmpty
                                    inputProps={{'aria-label': 'Without label'}}
                                >
                                    <MenuItem value="" style={{color: '#000'}}>
                                        
                                    </MenuItem>
                                    {/*<MenuItem style={{color: '#000'}} value={20}>English</MenuItem>*/}
                                </Select>
                            </FormControl>
                        </div>
                        {account.address &&
                        <div className="pool-txt">
                            <a
                                className="pool-txt-btn"
                                onClick={() => {
                                    if (rewardPool.tokens.length > 0) {
                                        navigateStake(rewardPool)
                                    }
                                }}
                            >Initial
                            </a>
                        </div>
                        }
                        {account.address &&
                        <div className="pool-txt">
                            <a
                                className="pool-txt-btn"
                                onClick={() => {
                                    if (rewardPoolDAI.tokens.length > 0) {
                                        navigateStake(rewardPoolDAI)
                                    }
                                }}
                            >Stable Pool
                            </a>
                        </div>
                        }
                        {account.address &&
                        <div className="pool-txt">
                            <a
                                className="pool-txt-btn"
                                onClick={() => {
                                    if (rewardPoolUSDT.tokens.length > 0) {
                                        navigateStake(rewardPoolKANI)
                                    }
                                }}
                            >Governance
                            </a>
                        </div>
                        }
                        <div className="item">
                            <a href={'https://twitter.com/eat_kani'} target="_blank">Twitter</a>
                        </div>
                        <div className="item">
                            <a href={'https://github.com/kani-fi/'} target="_blank">Github</a>
                        </div>
                        <div className="item">
                            <a href={'https://t.me/EAT_KANI'} target="_blank">Telegram</a>
                        </div>
                        {/*<div className="item">*/}
                            {/*<a>WeChat</a>*/}
                        {/*</div>*/}
                    </div>
                </div>
            </div>
            {/*<div className={ `${classes.stake}` } onClick={ () => { nav('staking') } }>*/
            }
            {/*<Typography variant={'h3'} className={ headerValue===0?`titleActive`:`title` }>Stake </Typography>*/
            }
            {/*</div>*/
            }
            {/*<div className={ `${classes.vote}` } onClick={ () => { nav('vote') } }>*/
            }
            {/*<Typography variant={'h3'} className={ headerValue===1?`titleActive`:`title` }>Vote </Typography>*/
            }
            {/*</div>*/
            }
        </div>
    )
}

